import React from "react";
import { Switch } from "antd";
import { Feature } from "types";
import useAppStore from "components/context/app/Hooks";

export default function AuthSwitch(props) {
  const { licensed } = useAppStore();

  return (
    <Switch
      {...props}
      defaultChecked={!licensed(Feature.Dashboard) && props?.value}
      checkedChildren="Enabled"
      unCheckedChildren="Disabled"
      disabled={!licensed(Feature.Dashboard)}
    />
  );
}
